import * as yup from "yup";
import { setLocale } from "yup";

setLocale({
  mixed: {
    default: "",
    required: "",
    oneOf: ({ values }) => `: ${values}`,
    notOneOf: ({ values }) => `: ${values}`,
    notType: "",
  },
  string: {
    length: ({ length }) => `${length}`,
    min: ({ min }) => `${min}`,
    max: ({ max }) => `${max}`,
    matches: "",
    email: "",
    url: "",
    trim: "",
    lowercase: "",
    uppercase: "",
  },
  number: {
    min: ({ min }) => `${min}`,
    max: ({ max }) => `${max}`,
    lessThan: ({ lessThan }) => `${lessThan}`,
    moreThan: ({ moreThan }) => `${moreThan}`,
    notEqual: ({ notEqual }) => `${notEqual}`,
    positive: "",
    negative: "",
    integer: "",
  },
  date: {
    default: "",
    min: ({ min }) => `${min}`,
    max: ({ max }) => `${max}`,
  },
  object: {
    noUnknown: "",
  },
  array: {
    min: ({ min }) => `${min}`,
    max: ({ max }) => `${max}`,
  },
});


export default yup;
